.form {
  padding: 20px;

  &__group:not(:last-child) {
    margin-bottom: 2rem;
  }

  &__label {
    font-size: $default-font-size;
    display: block;
    margin-bottom: 1rem;
  }

  &__input {
    padding: 1.2rem 2rem;
    border-radius: 2px;

    &:not(:last-child) {
      margin-bottom: 2rem;
    }
  }
}
